function User(name) {
	if (!(this instanceof User)) {
		throw new Error('Call without new!');
	}

	this.name = name;
	this.sayHi = function() {
		alert('My name ' + this.name);
	}
}

User.prototype.fn = function() {
	return 1;
}

var admin = new User('Admin');
